@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500&display=swap);
body {
  margin: 0 auto;
  box-sizing: border-box;
  font-family: 'Roboto Light', sans-serif;
}
.App {
  margin: 0 auto;
  width: 80%;
  text-align: center;
  box-sizing: border-box;
  font-family: "Raleway", sans-serif;
}

.pagination {
    display: flex;
    justify-content: flex-end;

}

.pagination p {
    margin: 8px;
}

.pagination button {
    background-color: transparent;
    border: none;
    color: black;
    padding: 8px 8px;
    text-decoration: none;
    cursor: pointer;
    width: 4%;
}

.pagination button:hover:not(.active) {
    background-color: #d6eaf8;
}

@media only screen and (max-width: 1130px) {
    .pagination {
        justify-content: center;
        width: 100%;
    }

    .pagination button {
        width: 10%;
    }
}

.tableContainer {
  margin: 0 auto;
  width: 80vw;
  padding-bottom: 3vh;
}

table {
  display: table;
  text-align: center;
  border-collapse: collapse;
  width: 100%;
  border-bottom: 2px solid #1a5276;
}

td {
  padding: 1vh;
}

th {
  padding: 0;
}

button {
  border: none;
  background-color: #1a5276;
  padding: 2vh;
  margin: 0;
  width: 100%;
  height: 100%;
  color: white;
  font-size: 1em;
  white-space: nowrap;
  cursor: pointer;
}

button.ascending::after {
  content: "↑";
  display: inline-block;
  padding-left: 1em;
}

button.descending::after {
  content: "↓";
  display: inline-block;
  padding-left: 1em;
}

tr:nth-child(odd) {
  background-color: #d6eaf8;
}

@media only screen and (max-width: 760px),
  (min-device-width: 768px) and (max-device-width: 1024px) {
  table,
  thead,
  tbody,
  th,
  td,
  tr {
    display: block;
  }

  thead tr button {
    border: 1px solid #fff;
  }

  table tr {
    border-bottom: 3px solid #1a5276;
    display: block;
    margin-bottom: 5px;
  }

  table td {
    border-bottom: 1px solid #1a5276;
    display: block;
    font-size: 0.8em;
    text-align: right;
  }

  table td::before {
    float: left;
  }

  /*
      Label the data
      */
  td:nth-of-type(1):before {
    content: "ID";
  }

  td:nth-of-type(2):before {
    content: "Name";
  }

  td:nth-of-type(3):before {
    content: "City";
  }

  td:nth-of-type(4):before {
    content: "Total Income";
  }
}

.companyView {
  display: flex;
  justify-content: center;
  align-items: center;
}

.companyView button {
}

.incomeData {
  width: 30%;
  margin: 5px;
}

.filtering {
    display: inline-block;
    padding-bottom: 2vh;
    float: left;
    margin: 0;
}

.filtering p {
    display: inline-block;
    margin: 8px;
}

@media only screen  and (max-width: 1130px) {
    .filtering {
        display: block;
        float: left;
    }

}
